import { makeStyles } from '@material-ui/styles';
import { pxToRem } from '../../../../../styles/themes/vad/constants';

const useStyles = makeStyles(theme => {
  const { right, left, paddingLR } = theme.mixins;
  return {
    root: {
      display: 'inline-block',

      '& .default-shrink': {
        position: 'absolute',
        transform: 'translate(0, -41.5px) scale(0.9)',
        ...left(pxToRem(-8)),
      },

      '& .react-datepicker-popper': {
        zIndex: 9,
        '& .react-datepicker': {
          borderRadius: 10,
          border: 'none',
          boxShadow: theme.shadows[1],
          width: '100%',
          display: 'flex',

          '& .react-datepicker__navigation': {
            border: 'solid',
            borderWidth: '0 2px 2px 0',
          },
          '& .react-datepicker__navigation--previous': {
            transform: 'rotate(138deg)',
            top: pxToRem(19),
            left: pxToRem(29),
          },
          '& .react-datepicker__navigation--next': {
            transform: 'rotate(314deg)',
            top: pxToRem(19),
            right: pxToRem(29),
          },
          '& .react-datepicker__triangle': {
            display: 'none',
          },
          '& .react-datepicker__month-container': {
            width: pxToRem(282),

            '& .react-datepicker__month': {
              ...theme.typography.labelRegular,
              '& .react-datepicker__day--in-range, .react-datepicker__day--in-selecting-range': {
                background: theme.palette.action.disabledBackground,
                color: theme.mixins.Black(),
                borderRadius: '0 !important',
                '&.react-datepicker__day--selecting-range-start': {
                  background: theme.mixins.Grey(),
                  borderRadius: '50% !important',
                  color: theme.mixins.White(),
                },
              },
              '& .react-datepicker__day--selecting-range-end, .react-datepicker__day--range-end, .react-datepicker__day--range-start': {
                background: theme.mixins.Grey(),
                borderRadius: '50% !important',
                color: theme.mixins.White(),
              },
              '& .react-datepicker__day': {
                height: pxToRem(36),
                width: pxToRem(36),
                borderRadius: '50%',
                paddingTop: 6,
                paddingBottom: 6,
                margin: '3px 0 3px 0',

                '&.react-datepicker__day--keyboard-selected': {
                  backgroundColor: theme.mixins.White(),
                  color: theme.mixins.Black(),

                  '&.react-datepicker__day--range-end, &.react-datepicker__day--range-start': {
                    background: theme.mixins.Grey(),
                    borderRadius: '50% !important',
                    color: theme.mixins.White(),
                  },
                },
              },
              '& .react-datepicker__day--today': {
                color: theme.palette.common.SeaPort,

                '&:hover': {
                  backgroundColor: theme.mixins.Grey(200),
                  color: theme.mixins.Black(),
                },
                '&.react-datepicker__day--range-end, &.react-datepicker__day--range-start': {
                  background: theme.mixins.Grey(),
                  borderRadius: '50% !important',
                  color: theme.mixins.White(),
                },
              },
              '& .react-datepicker__day--selected': {
                background: theme.mixins.Grey(),
                color: theme.mixins.White(),
              },
              '& .react-datepicker__day--outside-month': {
                color: theme.palette.action.disabled,
              },
            },
            '& .react-datepicker__header': {
              background: theme.mixins.White(),
              borderBottom: 'none',

              '& .react-datepicker__day-names .react-datepicker__day-name': {
                ...theme.typography.labelRegular,
                color: 'rgb(130, 136, 148)',
                marginTop: pxToRem(10),
                margin: 4,
              },
              '& .react-datepicker__current-month': {
                ...theme.typography.body2,
                marginTop: 5,
              },
            },
            '& .react-datepicker__day-name, .react-datepicker__day': {
              margin: '0.4rem',
            },
          },
        },
      },
      '& .calendarIcon': {
        position: 'absolute',
        width: pxToRem(16),
        height: pxToRem(16),
        top: pxToRem(16),
        ...right(pxToRem(20)),
      },
    },
    datePicker: {
      width: '100%',
      height: pxToRem(48),
      paddingLeft: pxToRem(16),
      border: theme.borders.fieldBorder,
      borderRadius: '3px',

      '&:hover, &:focus': {
        background: theme.palette.action.disabledBackground,
        backgroundColor: 'inherit',
      },
    },
    CalendarWrapper: {
      position: 'relative',
      marginTop: pxToRem(46),

      '& svg': {
        visibility: 'hidden',
      },
      '& .react-datepicker-wrapper': {
        display: 'block',

        '& .react-datepicker__input-container': {
          visibility: 'hidden',
        },
      },
    },
    rangeCalendar: {
      display: 'flex',
      position: 'relative',
    },
    rangeCalendarPicker: {
      position: 'absolute',
      width: '100%',
      cursor: 'pointer',
      ...left(0),
      top: pxToRem(46),
      height: pxToRem(48),
      paddingTop: pxToRem(13),
      ...paddingLR(pxToRem(16), 0),
      backgroundColor: theme.mixins.White(),
      border: theme.borders.fieldBorder,
      borderRadius: '3px',
      '&:hover': {
        background: theme.mixins.Black(10),
      },
    },
    labelFilled: {
      position: 'absolute',
      transform: 'translate(0, -41.5px) scale(0.9)',
      ...left(pxToRem(-8)),
    },
    endDate: {
      position: 'absolute',
      ...left(0),
    },
    errorMsg: {
      color: theme.palette.text.error,
      marginTop: pxToRem(8),
    },
    customPlaceholder: {
      color: theme.palette.text.tertiary,
    },
  };
});

export default useStyles;
