import React, { useState, useEffect } from 'react';
import { get } from 'lodash';

import { withNamespaces } from 'react-i18next';
import { Text } from '@sitecore-jss/sitecore-jss-react';
import { withSitecoreContext } from '@sitecore-jss/sitecore-jss-react';

import Grid from '../../../components/molecules/Grid';
import useStyles from './RequestForProposal.style';
import { COMPONENT_LOCATOR } from './locators';
import withErrorBoundary from '../../../utils/HOCs/withErrorBoundary';
import { getDictionaryText } from '../../../utils/getDictionaryText';
import RenderRFPForm from './RenderRFPForm';
import DctSvgIcon from '../../../components/atoms/Svg';
import RichText from '../../../components/atoms/RichText';
import { formFieldTypes } from './constants';
import { emailValidator } from '../../../utils/validationRules';
import fetchService from '../../../../services/FetchService';
import scrollTo from '../../../utils/scrollTo';
import Typography from '../../../components/atoms/Typography';
import {
  pushAnaylyticsData,
  createDynamicLabels,
} from '../../../../utils/analytics';
import {
  getPageName,
  getComponentName,
} from '../../../utils/getPageComponentInfo';
import { analyticsData } from '../../../../constants/Analytics/analytics-data';

const RequestForProposal = props => {
  const classes = useStyles(props);
  const formGroups = get(props, 'fields.formGroups', '');
  const [formDataList, setFormDataList] = useState({});
  const [errors, setFieldError] = React.useState({});
  const [recaptchachecked, setRecaptchachecked] = React.useState(false);
  const [apiCallStatus, setApiCallStatus] = React.useState(false);
  const [submissionStatus, setSubmissionStatus] = React.useState(false);
  const [disableFile, setDisableFile] = React.useState(false);
  const [isFormValid, setFormIsValid] = React.useState(false);
  const [failedToUploadMessage, setFailedToUploadMessage] = React.useState('');
  const captchaApiKey = getDictionaryText('googleCaptchaApiKey');
  const language = get(props, 'sitecoreContext.language', '');

  useEffect(() => {
    shouldValidateCaptcha();
    setFormIsValid(inValidFields.length === 0);
  });

  /* 
  * This method return true if child field is needed to be shown to user
  * Here we check if parent field value is matching with required value to show child field
  */
  const shouldAddInRequiredField = field => {
    const parentKey = get(field, 'parentKey', '');
    if (parentKey && parentKey.value) {
      const parentValue = get(field, 'parentValue.fields.key.value', '');
      const parentMappingValue = get(
        field,
        'parentValue.fields.mappingId.value',
        ''
      );
      if (
        formDataList[parentKey && parentKey.value] &&
        (formDataList[parentKey && parentKey.value] === parentValue ||
          formDataList[parentKey && parentKey.value] === parentMappingValue)
      ) {
        return true;
      } else {
        return false;
      }
    } else {
      return true;
    }
  };

  // renders typography based on type provided
  const renderText = (variant, type, value, className) => (
    <Typography
      variant={variant}
      component={type}
      gutterBottom
      className={className}
    >
      <Text field={value} />
    </Typography>
  );

  // Extracting required fields for form validation
  let requiredFields = [];
  formGroups.forEach(formGroup => {
    formGroup.fields.fields.forEach(({ fields }) => {
      if (fields.fieldGroup && fields.fieldGroup.length) {
        fields.fieldGroup.forEach(groupField => {
          if (
            groupField.fields.isRequired &&
            groupField.fields.isRequired.value
          ) {
            // We will add child fields only if their parent is selected with the required value
            if (shouldAddInRequiredField(groupField.fields)) {
              requiredFields.push(groupField.fields);
            }
          }
        });
      } else {
        if (fields.isRequired && fields.isRequired.value) {
          // We will add child fields only if their parent is selected with the required value
          if (shouldAddInRequiredField(fields)) {
            requiredFields.push(fields);
          }
        }
      }
    });
  });
  const inValidFields = requiredFields
    .map(fields => {
      if (
        fields.fieldType &&
        fields.fieldType.value === formFieldTypes.RANGEDATE
      ) {
        const fieldName = fields.fieldName && fields.fieldName.value.split('|');
        if (
          !formDataList[fieldName && fieldName[0]] ||
          !formDataList[fieldName && fieldName[1]]
        ) {
          return false;
        }
      } else if (
        !formDataList[fields.fieldName.value] ||
        formDataList[fields.fieldName.value].length === 0
      ) {
        if (
          fields.defaultValue &&
          fields.defaultValue.fields.displayText.value
        ) {
          return setFormDataList({
            ...formDataList,
            [fields.fieldName.value]:
              fields.defaultValue.fields.displayText.value,
          });
        }
        return false;
      }
      if (errors['invalidEmail']) {
        return false;
      }
      if (captchaApiKey) {
        return recaptchachecked;
      }
      return true;
    })
    .filter(value => !value);
  const recaptchaCallback = value => {
    //If value is long string then we have sucessfully submited
    // google captcha and now we can submit the form
    setRecaptchachecked(value.length > 0);
  };

  const shouldValidateCaptcha = () => {
    let hasCaptcha = false;
    formGroups.forEach(formGroup => {
      formGroup.fields.fields.forEach(({ fields }) => {
        if (fields.fieldGroup && fields.fieldGroup.length) {
          fields.fieldGroup.forEach(groupField => {
            if (groupField.enableCaptcha && groupField.enableCaptcha.value) {
              hasCaptcha = true;
            }
          });
        } else {
          if (fields.enableCaptcha && fields.enableCaptcha.value) {
            hasCaptcha = true;
          }
        }
      });
    });

    if (!hasCaptcha) {
      setRecaptchachecked(true);
    }
    return hasCaptcha;
  };

  const onRemoveClick = (key, name) => {
    let fileData = formDataList[name];
    fileData = fileData.filter(file => file.lastModified !== key);
    setFormDataList({
      ...formDataList,
      [name]: fileData,
    });
  };

  // render form sections
  const renderFormFields = () => {
    return formGroups.map(formGroup => {
      const formData = get(formGroup, 'fields', {});
      const id = get(formGroup, 'id', {});
      return (
        <RenderRFPForm
          formData={formData}
          key={id}
          formDataList={formDataList}
          onChange={handleChange}
          handleCheckboxChange={handleCheckboxChange}
          captchaApiKey={captchaApiKey}
          recaptchaCallback={recaptchaCallback}
          isFormValid={isFormValid}
          errors={errors}
          onRemoveClick={onRemoveClick}
          disableFile={disableFile}
        />
      );
    });
  };

  const checkFilesValidty = (file, target) => {
    const suportedFormats = getDictionaryText('supportedUploadFiles');
    const maxFileUploadSize =
      getDictionaryText('maxFileUploadSize') &&
      parseInt(getDictionaryText('maxFileUploadSize'));
    const fileFormats = suportedFormats && suportedFormats.split(',');

    if (
      !fileFormats.filter(
        fileFormat =>
          file.type.includes(fileFormat) || file.name.includes(fileFormat)
      ).length
    ) {
      setFieldError({
        ...errors,
        fileError: 'fileType',
      });
      return false;
    } else if (file.size > maxFileUploadSize) {
      setFieldError({
        ...errors,
        fileError: 'fileSize',
      });
      return false;
    } else if (
      formDataList[target.name] &&
      formDataList[target.name].length === 5
    ) {
      setFieldError({
        ...errors,
        fileError: 'fileSize',
      });
      return false;
    } else if (formDataList[target.name] && formDataList[target.name].length) {
      let totalFilesSize = file.size;
      formDataList[target.name].forEach(file => {
        totalFilesSize += file.size;
      });
      if (totalFilesSize > maxFileUploadSize) {
        setFieldError({
          ...errors,
          fileError: 'fileSize',
        });
        return false;
      }
    }
    setFieldError({
      ...errors,
      fileError: '',
    });
    return true;
  };

  //handle form value change
  const handleChange = e => {
    if (e.target && e.target.type === 'file') {
      try {
        if (e.target.files.length) {
          const files = e.target.files[0];
          const targetName = e.target.name;
          if (checkFilesValidty(files, e.target)) {
            const formData = new FormData();
            formData.append('files', files);
            setDisableFile(true)
            fetchService({
              url: props.appConfig && props.appConfig.RFP_API_VIRUS_CHECK,
              method: 'POST',
              headers: { 'Content-Type': 'multipart/form-data' },
              data: formData,
            })
              .then(response => {
                setDisableFile(false);
                if (response && response.status === 200) {
                  if (response && response.data.code === 9000) {
                    let uploadErrorMessage = getDictionaryText(response.data.code);
                    uploadErrorMessage = uploadErrorMessage.replace('{fileName}', response.data.data)
                    setFieldError({
                      ...errors,
                      uploadError: uploadErrorMessage,
                    });
                  } else {
                    if (formDataList[targetName]) {
                      setFormDataList({
                        ...formDataList,
                        [targetName]: [...formDataList[targetName], files],
                      });
                    } else {
                      setFormDataList({
                        ...formDataList,
                        [targetName]: [files],
                      });
                    }
                  }
                }
              })
              .catch(err => {
                setDisableFile(false);
                console.log(err);
              })
          }
        }
      } catch (err) {
        console.log(err);
      }
    } else {
      setFormDataList({
        ...formDataList,
        [e.target.name]: e.target.value,
      });
      if (e.target.name.includes('email')) {
        if (
          formDataList[e.target.name] !== e.target.value &&
          !emailValidator(e.target.value)
        ) {
          return setFieldError({
            ...errors,
            invalidEmail: true,
          });
        }
        return setFieldError({
          ...errors,
          invalidEmail: false,
        });
      }
    }
  };

  //handle form value change
  const handleCheckboxChange = e => {
    setFormDataList({
      ...formDataList,
      [e.target.name]: e.target.checked,
    });
  };

  const getFormDataByFieldName = (formData, category, fieldName) => {
    if (fieldName === 'frequency' || fieldName === 'abuDhabhiEventsCount') { // This is just to remove underscore for the last element
      return `${get(formData, `${category}.${fieldName}`, '')}`;
    }
    return `${get(formData, `${category}.${fieldName}`, '')}_`;
  }

  // handles rfp form submission for successs or failure
  const handleSubmit = e => {
    e.preventDefault();
    // Analytics data capturing on success submit
    const obj = {
      ...analyticsData.pageBodyInteraction.rfpFormSubmit,
      ...{
        label: createDynamicLabels([getPageName(props),
        getComponentName(props),
        `${getFormDataByFieldName(formDataList, 'personalInfo', 'country')}${getFormDataByFieldName(formDataList, 'organizationInfo', 'type')}${getFormDataByFieldName(formDataList, 'eventInfo', 'type')}${getFormDataByFieldName(formDataList, 'eventInfo', 'frequency')}`,
        ]),
        label2: createDynamicLabels([getPageName(props),
        getComponentName(props),
        `${getFormDataByFieldName(formDataList, 'organizationInfo', 'abroadEventsCount')}${getFormDataByFieldName(formDataList, 'organizationInfo', 'abuDhabhiEventsCount')}`,
        ]),
      },
    };
    pushAnaylyticsData(obj);
    const form = document.querySelector('#requestForProposal');
    const formData = new FormData(form);
    formData.append('language', language);
    formData.delete('upload');
    formDataList.upload &&
      formDataList.upload.forEach(data => {
        formData.append('files', data);
      });
    try {
      fetchService({
        url: props.appConfig && props.appConfig.RFP_API,
        method: 'POST',
        data: formData,
      }).then(response => {
        if (response && response.status === 200) {
          if (response && (response.data.code === 9001 || response.data.code === 9002)) {
            setFailedToUploadMessage(getDictionaryText(response.data.code));
          } else {
            setApiCallStatus(response.data.data);
            setSubmissionStatus(true);
            setTimeout(() => {
              const componentRootElm =
                document.querySelector('#rfpSection').offsetTop - 150;
              scrollTo(componentRootElm);
            }, 80);
          }
        } else {
          setApiCallStatus(false);
          console.error(response.error);
          setSubmissionStatus(true);
          setTimeout(() => {
            const componentRootElm =
              document.querySelector('#rfpSection').offsetTop - 150;
            scrollTo(componentRootElm);
          }, 80);
        }
      });
    } catch (err) {
      console.error('RFP Form failure', err);
    }
  };

  const submissionMessages = () => {
    const groupLength = formGroups.length;
    const submitFields = formGroups[groupLength - 1].fields.fields.filter(
      field =>
        field.fields.fieldType &&
        field.fields.fieldType.value === formFieldTypes.SUBMIT
    );
    const failureMessage = get(
      submitFields[0],
      'fields.submitActions[0].fields.failureMessage',
      ''
    );
    let successMessage = get(
      submitFields[0],
      'fields.submitActions[0].fields.successMessage',
      ''
    );

    if (apiCallStatus) {
      if (successMessage.value) {
        successMessage.value = successMessage.value.replace(
          '{referenceId}',
          apiCallStatus.referenceNumber
        );
      }
      return (
        <React.Fragment>
          <DctSvgIcon name="SuccessIcon" />
          <RichText
            innerHTML={successMessage}
            className="formSubmissionMessage"
          />
        </React.Fragment>
      );
    } else {
      return (
        <React.Fragment>
          <DctSvgIcon name="FailureIcon" />
          <RichText
            innerHTML={failureMessage}
            className="formSubmissionMessage"
          />
        </React.Fragment>
      );
    }
  };

  return (
    <Grid
      item
      className={classes.root}
      data-locator={COMPONENT_LOCATOR}
      id="rfpSection"
    >
      <Grid item className={classes.formContainer}>
        {!submissionStatus ? (
          <form
            className="rfp-form-wrapper"
            onSubmit={handleSubmit}
            noValidate
            autoComplete="off"
            id="requestForProposal"
            encType="multipart/form-data"
          >
            {renderFormFields()}
            {failedToUploadMessage ? renderText('body3', 'div', { value: failedToUploadMessage }, classes.uploadError) : ''}
          </form>
        ) : (
            <div className={classes.messageWrapper}>
              <div className={classes.submissionMessage}>
                {submissionMessages()}
              </div>
            </div>
          )}
      </Grid>
    </Grid>
  );
};

export default withErrorBoundary(
  withSitecoreContext()(withNamespaces()(RequestForProposal))
);
