import { makeStyles } from '@material-ui/core';
import { pxToRem } from '../../../../../styles/themes/vad/constants';

const useStyles = makeStyles(theme => ({
  root: {
    '&.MuiTextField-root, & .MuiFormControl-root': {
      marginBottom: pxToRem(24),
      width: '100%',
      [theme.breakpoints.up('lg')]: {
        margin: theme.spacing(1),
        width: 'calc(50% - 16px)',
        marginBottom: pxToRem(32),
        marginTop: 0,

        '&.fullWidth': {
          width: 'calc(100% - 16px)',
        },
      },

      '& .MuiFormLabel-root': {
        [theme.breakpoints.up('lg')]: {
          lineHeight: pxToRem(32),
        },
      },

      '& .MuiFormLabel-root, & .MuiTypography-root': {
        fontSize: pxToRem(14),
        position: 'relative',
        transform: 'translate(0, -8px) scale(1)',
        ...theme.mixins.left(0),
        [theme.breakpoints.up('lg')]: {
          transform: 'translate(0, 0px) scale(1)',
        },

        '&.Mui-error': {
          color: 'inherit',
        },

        '& .MuiTypography-root': {
          transform: 'translate(0, -23px) scale(1)',
        },
      },

      '& .MuiFormHelperText-root': {
        ...theme.typography.body3,
        ...theme.mixins.marginLeft(0),
        marginTop: pxToRem(8),
        color: theme.mixins.Black(),
        '&.Mui-required': {
          marginTop: pxToRem(10),
          color: theme.mixins.AbuDhabiRed(),
        },
      },

      '& .MuiInputBase-formControl': {
        border: theme.borders.fieldBorder,
        height: pxToRem(48),
        borderRadius: pxToRem(3),
        marginTop: 0,
        '&.Mui-error': {
          borderColor: theme.palette.border.error,
        },
        '&:hover': {
          background: theme.mixins.Black(30),
          '&::placeholder': {
            color: theme.mixins.Black(),
          },
        },
        '&.MuiInputBase-multiline': {
          height: 'auto',
          padding: 0,
        },
        '& .MuiInputBase-inputMultiline': {
          height: pxToRem(96),
          padding: pxToRem(16),

          '&:before': {
            border: 0,
          },
        },
        '& input, & textarea': {
          padding: `0 ${pxToRem(16)}`,
          fontSize: pxToRem(16),
          height: '100%',

          '&::placeholder': {
            color: theme.palette.text.tertiary,
          },
          '&::-webkit-input-placeholder': {
            /* Chrome/Opera/Safari */
            color: theme.palette.text.tertiary,
            opacity: 1,
          },
          '&::-moz-placeholder': {
            color: theme.palette.text.tertiary,
            opacity: 1,
          },
          '&:-ms-input-placeholder': {
            /* IE 10+ */
            color: theme.palette.text.tertiary,
            opacity: 1,
          },
          '&:-moz-placeholder': {
            /* Firefox 18- */
            color: theme.palette.text.tertiary,
            opacity: 1,
          },
        },
        '& fieldset': {
          border: 0,
        },
      },
    },
  },
}));

export default useStyles;
